.btn {
  border: none;
  outline: none;
  background: none;
}

.btn-disable {
    color: rgba(255, 255, 255, 0.37);
}

.content__start-btn {
    padding: 22px;
    background-color: var(--primary-color);
    cursor: pointer;
    transition: all ease 0.3s;
    margin: 15px 0;
}

.content__start-btn:hover {
    transform: translateY(-10%);
    background-color: rgb(15, 204, 141);
    box-shadow: 0 1px 5px #333;
}

.content__start-link {
    color: #fff;
    font-weight: bold;
}