.modalContainer {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 1000;
    display: none;
}

.modalOverlay {
    position: relative;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: inherit;
    animation: fadeIn 0.3s ease-in-out;    
}

.modalBox {
    border-radius: 10px;
    min-height: 24rem;
    width: 42rem;
    background-color: white;
    z-index: 1001;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: fadeIn 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
}

.modalPhoto {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    padding-top: 40%;
}

.modalTextWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 5px 54px 25px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.modalText {
    text-align: center;
    transition: all ease 0.2s;
    font-family: Montserrat, sans-serif;
    font-size: 1.4rem;
}

.modalText:hover {
    color: var(--primary-color);
}

.modalClose {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    display: flex;
    justify-content: flex-end;
}

.modalCloseBtn {
    font-size: 2rem;
    border-top-right-radius: 10px;
    padding: 5px 15px;
    margin-top: 5px;
    cursor: pointer;
    transition: all ease 0.2s;
}

.modalCloseBtn:hover {
    color: var(--primary-color);
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

