main {
    height: 100vh;
    width: 100vw;
}

#homepage {
    height: 100%;
    width: 100%;
    background: linear-gradient(to bottom right, #303d46, #272c2c);  
    background: -webkit-linear-gradient(to left, #303d46, #272c2c);  
    position: relative;
    z-index: 2;
}

#homepageHeader {
    padding: 13.5px;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    background: transparent;  
    z-index: 2;  
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all ease 0.2s;
}

.headerLogo {
    font-size: 2.3rem;
    color: #fff;
    font-family: 'Dancing Script', sans-serif;
    display: inline-block;
    padding: 4.5px;
    margin-left: 8px;
}

.headerBrand {
    font-weight: 300;
}

.headerBrandCV {
    color: var(--primary-color);
}

#backgroundVideo {
    width: 100%;
    outline: none;
    height: 100%;
}

#homeContent {
    padding-top: 83px;
    position: relative;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
}

.reviewSection {
    display: flex;
    margin: 0 6rem;
    align-items: center;
    padding-top: 5rem;
}

.homeBackgroundImg {
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    height: 100%;
    width: 100%;
    padding-top: 35%;
    position: relative;
    z-index: 2;
    transition: all ease 5s;
    animation: floating-1d-reverse 7s ease infinite;
    -webkit-animation: floating-1d-reverse 7s ease infinite;
    -moz-animation: floating-1d-reverse 7s ease infinite;
    -ms-animation: floating-1d-reverse 7s ease infinite;
}

@keyframes floating-1d-reverse {
    0% {
        transform: translate(-5px, 40px) scale(0.9);
        -webkit-transform: translate(-5px, 40px) scale(0.9);
        -moz-transform: translate(-5px, 40px) scale(0.9);
        -ms-transform: translate(-5px, 40px) scale(0.9);
    }
    50% {
        transform: translate(10px, 0px) scale(1);
        -webkit-transform: translate(10px, 0px) scale(1);
        -moz-transform: translate(10px, 0px) scale(1);
        -ms-transform: translate(10px, 0px) scale(1);
    }
    100% {
        transform: translate(-5px, 40px) scale(0.9);
        -webkit-transform: translate(-5px, 40px) scale(0.9);
        -moz-transform: translate(-5px, 40px) scale(0.9);
        -ms-transform: translate(-5px, 40px) scale(0.9);
    }
}

@keyframes floating-1d {
    0% {
        transform: translateY(0px);
        -webkit-transform: translateY(0px);
        -moz-transform: translateY(0px);
        -ms-transform: translateY(0px);
    }
    50% {
        transform: translateY(30px);
        -webkit-transform: translateY(30px);
        -moz-transform: translateY(30px);
        -ms-transform: translateY(30px);
    }
    100% {
        transform: translateY(0px);
        -webkit-transform: translateY(0px);
        -moz-transform: translateY(0px);
        -ms-transform: translateY(0px);
    }
}

.contentStart {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    color: #15efd9;
    animation: floating-1d 4s linear infinite;
    -webkit-animation: floating-1d 4s linear infinite;
    -moz-animation: floating-1d 4s linear infinite;
    -ms-animation: floating-1d 4s linear infinite;
    position: relative;
    z-index: 2;
}

.contentStart:hover,
.homeBackgroundImg:hover {
    animation-play-state: paused;
    -webkit-animation-play-state: paused;
    -moz-animation-play-state: paused;
    -ms-animation-play-state: paused;
}

.startHeading {
    font-size: 5rem;
    margin-bottom: 4.5px;
    text-shadow: 0 14px 8px #1818189c;
    font-weight: 400;
}

.startSlogan {
    color: #fff;
    font-size: 2rem;
    margin: 9px 0 40.5px;
    padding: 10px 0;
    max-width: 53rem;
}

.startBtn, .navItemStart {
    max-height: 36px;
    max-width: 127px;
    padding: 18px 40.5px;
    font-size: 2rem;
    background-color: var(--primary-color) !important;
    border-radius: 10px;
    color: #fff;
    cursor: pointer;
    transition: all ease 0.2s;
    transform-origin: center;
    -webkit-transform-origin: center;
    -moz-transform-origin: center;
    -ms-transform-origin: center;
    position: relative;
    z-index: 2;
}

.startBtn:hover,
.navItem:hover {
    transform: scale(1.05);
    -webkit-transform: scale(1.05);
    -moz-transform: scale(1.05);
    -ms-transform: scale(1.05);
    box-shadow: 0 2px 8px rgb(19, 18, 18);
    background-color: #1b8d82;
}


.homeNavList {
    list-style: none;
}

.navItem {
    transition: all ease 0.3s;
}

.navItemStart {
    padding: 9px 18px;
    max-height: 27px;
    font-size: 1.4rem;
    border-radius: 5px;
    font-weight: 500;
}


/* Animated background CSS */
.circles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 1;
}

.circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    animation: boxAnimate 25s linear infinite;
    -webkit-animation: boxAnimate 25s linear infinite;
    -moz-animation: boxAnimate 25s linear infinite;
    -ms-animation: boxAnimate 25s linear infinite;
    bottom: -150px;
    
}

.circles li:nth-child(1){
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}

.circles li:nth-child(2){
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.circles li:nth-child(3){
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
}

.circles li:nth-child(4){
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.circles li:nth-child(5){
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.circles li:nth-child(6){
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.circles li:nth-child(7){
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.circles li:nth-child(8){
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.circles li:nth-child(9){
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.circles li:nth-child(10){
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}

@keyframes boxAnimate {
    0%{
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }
}
