#photoContainer {
    height: 35mm;
    width: 35mm;
    position: relative;
    border-radius: 3px;
    margin-bottom: 8mm;
}

#photoContainer:hover > #uploadPhotoBtn {
    display: flex;
    background-color: rgba(204, 204, 204, 0.274);
}

#photoPlaceholder {
    height: 100%;
    width: 100%;
    border-radius: 3px;
}

#photoUpload {
    display: none;
}

#uploadPhotoBtn {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 22pt;
    color: rgb(102, 102, 102);
    display: none;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 3px;
    background-color: transparent;
}


