.lineSelector {
    position: absolute;
    width: 120%;
    min-height: 100%;
    top: 50%;
    left: 50%;
    right: 0;
    transform: translate(-50%, -50%);
    display: none;
    justify-content: space-between;
    font-size: 1.4rem;
    background-color: rgba(255, 255, 255, 0.7);
    box-shadow: 0 0 2.1166666667mm rgba(0, 0, 0, 0.7);
    border-radius: 1.3229166667mm;
    z-index: 2;
    padding: 2.6458333333mm 0;
    align-items: center;
}

.lineBtn {
    color: rgb(88, 87, 87);
    font-size: 2.5rem;
    cursor: pointer;
    padding: 0 2.6458333333mm;
    position: relative;
    max-height: 26px;
}

.lineBtn:hover {
    color: var(--primary-color)
}

.btnNote {
    position: absolute;
    font-size: 1.2rem;
    background-color: #fff;
    color: rgba(0, 0, 0, 0.719);
    top: 110%;
    transform: translateY(70%);
    min-width: 9rem;
    padding: 5px;
    border-radius: 5px;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.685);
    font-weight: bold;
}

#addLineBtn:hover .left-note,
#dltLineBtn:hover .right-note {
    display: block;
}

.right-note {
    left: 20%;
    display: none;
}

.right-note::after,
.left-note::after {
    content: "";
    display: block;
    width: 0;
    height: 0;
    top: -50%;
    position: absolute;
    border: 10px solid transparent;
    border-top: 0;
    border-bottom: 15px solid #fff;
}

.left-note {
    right: 10%;
    display: none;
    min-width: 11rem;
}

.left-note::after {
    left: 75%;
}
