* {
    padding: 0;
    margin: 0;
}

:root {
    --text-color: rgb(151, 147, 147);
    --toolbar-height: 48px;
    --primary-color: #1dbfaf;
    --primary-dark-color: rgb(71, 144, 153);
    --page-font: 'Montserrat', sans-serif;
    --primary-background-color: #303d46;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;
    font-family: Open Sans, sans-serif;
    scroll-behavior: smooth;
    user-select: none;
}

body {
    overflow-x: hidden;
}

a {
    text-decoration: none;
    color: var(--text-color);
}

.max-width-wide {
    width: 135.317904mm !important;
}

.active, .font-active {
    color: var(--primary-color) !important;
}

.visible {
    visibility: visible !important;
}

.hidden {
    visibility: hidden !important;
}

.d-flex {
    display: flex !important;
}
.d-block {
    display: block !important;
}

.zIndex-2 {
    z-index: 2;
}

.flex-1 {
    flex: 1;
}

.wide-90-percent {
    width: 90% !important;
}

/* Edit status */

.editMode:hover {
    outline: 2px dashed var(--primary-color);
    border-radius: 3px;
    filter: blur(0.75px);
    background-color: rgba(235, 233, 233, 0.404);
    color: #000;
}

.editMode:focus {
    outline: 2px solid var(--primary-color);
    /* filter: blur(0.7px); */
    box-shadow: 0 0 12px #ccc;
    background-color: #fff;
    border-radius: 3px;
    filter: none;
    color: #000;
}

/* Tag Customization */
.tag {
    padding: 2px 5px;
    border-radius: 5px;
    margin: 0 5px;
}

.available {
    background-color: #1dbfaf !important;
    color: #fff !important;
}

.not-available {
    background-color: #db1e1e !important;
    color: #fff !important;
}

.image-active {
    outline: 4px solid var(--primary-color) !important;
}

/* Font and color for LOGO */
.logo {
    font-family: 'Dancing Script', cursive;
    color: #fff;
    font-weight: 300;
    display: inline-block;
}

.logo-cv {
    color: var(--primary-color);
}


/* Font size for TEXT */
.txt-size-normal {
    font-size: 11pt;
}

/* HEAD NAVBAR FOR EDITOR */
#header-navbar {
    height: var(--toolbar-height);
    width: 100vw;
    font-family: inherit;
    background-color: #384954;
    box-shadow: 0 3px 10px rgb(0 0 0 / 38%);
    border-bottom: 1px solid rgb(40 37 37 / 48%);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    letter-spacing: 1px;
}

.nav__tools-container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    height: inherit;
    width: 774.80314961px;
    background-color: #384954;
    margin-left: auto;
    margin-right: auto;
}

.nav__logo {
    font-size: 2.4rem;
    color: #fff;
    font-weight: 300;
}

.nav__tools-home {
    display: flex;
    align-items: center;
    height: 100%;
}

.nav__tool-list {
    display: flex;
    height: 100%;
    list-style: none;
    align-items: center;
}

.nav__tool-item {
    margin: 0 25px;
}

.nav__design {
    display: flex;
    margin-top: auto;
    margin-bottom: auto;
    position: relative;
}

.nav__design::after {
    content: "";
    display: block;
    height: 25px;
    width: 1px;
    background-color: #fff;
    position: absolute;
    top: 50%;
    right: -16px;
    transform: translateY(-50%);
}

.nav__design::before {
    content: "";
    display: block;
    height: 25px;
    width: 1px;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: -16px;
    transform: translateY(-50%);
}

.nav__design-btn {
    padding: 6px 12px;
    cursor: pointer;
    background: none;
    font-size: 1.6rem;
    margin-right: 1px;
    margin-left: 1px;
    color: #fff;
    border-radius: 5px;
    transition: all ease 0.2s;
}

.nav__design-btn:hover {
    background-color: rgba(241, 239, 239, 0.2);
}

.nav__tool-zoom {
    display: flex;
    margin-top: auto;
    margin-bottom: auto;
}

.nav__tool-btn {
    padding: 4px 8px;
    cursor: pointer;
    background: none;
    font-size: 1.6rem;
    color: #fff;
    transition: all ease 0.1s;
}

.nav__tool-btn:hover {
    color: var(--primary-color);
}

.nav__font,
.nav__template {
    background: none;
    color: #ffff;
    display: flex;
    margin-top: auto;
    margin-bottom: auto;
    cursor: pointer;
    transition: all ease 0.1s;
    position: relative;
    font-size: 1.2rem;
    transform: translateY(3px);
    align-items: center;
}

.nav__font:hover,
.nav__template:hover {
    color: var(--primary-color);
}

.nav__font-txt,
.nav__template-txt {
    margin-left: 5px;
}

.slick-list {
    margin: 20px !important;
}

#font-submenu,
#template-submenu {
    background-color: #303d46;
    color: #fff;
    padding: 10px;
    min-width: 235px;
    min-height: 165px;
    border-radius: 10px;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    transform: translateY(10%);
    box-shadow: 0 0 0.5rem 0.1rem rgb(0 0 0 / 50%);
    cursor: default;
    z-index: 1;
    visibility: hidden;
} 

#template-submenu {
    width: 1000px;
    left: 100%;
    transform: translate(-50%, 7%);
    z-index: 2 !important;
}
  
.font-submenu__locate,
.template-submenu__locate { 
    color: #ccc;
    font-style: italic;
    font-size: 1.1rem;
}
  
.slider-nav {
    display: flex;
}

.slick__template-list {
    padding: 8px 0 0 0;
}
  
.slick__fonts-list,
.slick__template-list {
    list-style: none;
    color: #fff !important;
}

.slick__template-item {
    margin: 0 11px;
    float: left;
    cursor: pointer;
}

.slick__template-item:hover {
    transform: scale(1.03);
    transform-origin: center;
}

.slick__template-img {
    background-repeat: no-repeat;
    background-size: contain;
    padding-top: 75%;
    width: 208px;
}

.slick__template-name {
    font-size: 1.4rem;
    text-align: center;
    margin-top: 21px;
    padding-bottom: 5px;
    font-weight: 500;
}
  
.slick__fonts-item {
    padding: 4px 1px;
    cursor: pointer;
    font-size: 1.3rem;
}
  
.slick__fonts-item:hover {
    color: #1dbfaf;
}
  
.nav__slick-control {
    font-size: 1.6rem;
    cursor: pointer;
    display: inline-block;
    color: rgba(255, 255, 255, 0.493);
    height: 50px;
    margin: auto 0;
}
  
.nav__slick-control:hover {
    color: #1dbfaf;
}

.nav__download {
    display: flex;
    font-size: 1.3rem;
    align-items: center;
    height: 100%;
    min-width: 98.031px;
    background-color: #1dbfaf;
    color: #fff;
    padding: 0 12px;
    cursor: pointer;
    transition: all ease 0.3s;
    position: relative;
    justify-content: flex-end;
}

.nav__download:hover {
    background-color: #1ca99b;
}

.nav__download-txt {
    padding-left: 10px;
}

#page-content {
    overflow: hidden;
    width: 100vw;
    height: 100%;
    background-color: var(--primary-background-color);
    margin-top: var(--toolbar-height);
    transform-origin: top;
    transition: all ease-in-out 0.2s;
}

.page-content-wrapper {
    min-height: 2000px;
    width: 100vw;
    background-color: var(--primary-background-color);
}

/* Loader CSS */
#loader-wrapper {
	display: none;
    position: absolute;
}

.loader-4 {
	height: 32px;
	width: 32px;
}
.loader-4::after {
	content: "";
	display: block;
	position: absolute;
	margin: auto;
	width: 12px;
	height: 12px;
	top: 0; left: -144px;
	bottom: 0; right: 0;
	background: rgba(255, 255, 255, 0.753);
	border-radius: 50%;
	-webkit-animation: loader-4-1 2s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
	        animation: loader-4-1 2s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
}
@-webkit-keyframes loader-4-1 {
	0%   { -webkit-transform: scale(0); opacity: 0; }
	50%  { -webkit-transform: scale(1); opacity: 1; }
	100% { -webkit-transform: scale(0); opacity: 0; }
}
@keyframes loader-4-1 {
	0%   { transform: scale(0); opacity: 0; }
	50%  { transform: scale(1); opacity: 1; }
	100% { transform: scale(0); opacity: 0; }
}
.loader-4 span {
	display: block;
	position: absolute;
	top: 0; left: -144px;
	bottom: 0; right: 0;
	margin: auto;
	height: 32px;
	width: 32px;
	-webkit-animation: loader-4-2 2s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
	        animation: loader-4-2 2s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
}
@-webkit-keyframes loader-4-2 {
	0%   { -webkit-transform: rotate(0deg); }
	50%  { -webkit-transform: rotate(180deg); }
	100% { -webkit-transform: rotate(360deg); }
}
@keyframes loader-4-2 {
	0%   { transform: rotate(0deg); }
	50%  { transform: rotate(180deg); }
	100% { transform: rotate(360deg); }
}
.loader-4 span::before,
.loader-4 span::after {
	content: "";
	display: block;
	position: absolute;
	top: 0; left: 0;
	bottom: 0; right: 0;
	margin: auto;
	height: 12px;
	width: 12px;
	background: rgba(255, 255, 255, 0.753);
	border-radius: 50%;
	-webkit-animation: loader-4-3 2s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
	        animation: loader-4-3 2s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
}
@-webkit-keyframes loader-4-3 {
	0%   { -webkit-transform: translate3d(0, 0, 0) scale(1); }
	50%  { -webkit-transform: translate3d(-16px, 0, 0) scale(.5); }
	100% { -webkit-transform: translate3d(0, 0, 0) scale(1); }
}
@keyframes loader-4-3 {
	0%   { transform: translate3d(0, 0, 0) scale(1); }
	50%  { transform: translate3d(-16px, 0, 0) scale(.5); }
	100% { transform: translate3d(0, 0, 0) scale(1); }
}
.loader-4 span::after {
	-webkit-animation: loader-4-4 2s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
	        animation: loader-4-4 2s cubic-bezier(0.770, 0.000, 0.175, 1.000) infinite;
}
@-webkit-keyframes loader-4-4 {
	0%   { -webkit-transform: translate3d(0, 0, 0) scale(1); }
	50%  { -webkit-transform: translate3d(16px, 0, 0) scale(.5); }
	100% { -webkit-transform: translate3d(0, 0, 0) scale(1); }
}
@keyframes loader-4-4 {
	0%   { transform: translate3d(0, 0, 0) scale(1); }
	50%  { transform: translate3d(16px, 0, 0) scale(.5); }
	100% { transform: translate3d(0, 0, 0) scale(1); }
}