.cvHeader {
    text-align: center;
    padding: 6.6mm 6.6mm 0;
    margin-bottom: 5mm;
}

.cvContent {
    padding: 0 6.6mm;
}

.cvHeading {
    font-size: 24pt;
}

.cvJobDesc {
    font-size: 11pt;
}

.unitTitleWrapper {
    padding-bottom: 1.0583333333mm;
    margin: 2.1166666667mm 0 2.6458333mm;
}   

.unitTitle {
    font-size: 1.8rem;
    text-align: center;
    border-bottom: 1px solid #ccc;
    padding-bottom: 7px;
}

.unitBody {
    line-height: 15pt;
}

.unitLine {
    display: flex;
    font-size: 10pt;
    margin-bottom: 1.06mm;
    position: relative;
}

.lineTitleWrapper {
    width: 37.458333333mm;
}

.lineTitle {
    font-weight: bold;
    min-width: 26.458333333mm;
    display: block;
    padding: 0 3%;
    margin: 0 -3%;
}

.lineDescription {
    flex: 1;
    margin-left: 2.4971375mm;
    padding: 0 1mm;
    max-width: 154.85559792mm;
}

.lineText {
    font-size: 10pt;
    text-align: justify;
}

.lineSubtitle {
    font-weight: 600;
    line-height: inherit;
    margin-bottom: 1mm;
}

.lineList {
    padding: 0 4.5mm;
}

.cvFooter {
    flex: 1;
    padding: 0 6.6mm 6.6mm;
    display: flex;
    align-items: flex-end;
    margin-top: 6.6mm;
}

.copyrightText {
    color: rgb(143, 142, 142);
    font-size: 10pt;
    font-weight: 300;
    display: flex;
    align-items: center;
}

.copyrightText i {
    margin-right: 0.5mm;
}
