.page {
    transition: all ease 0.2s;
    background: white;
    width: 210mm;
    min-height: 297mm;
    display: block;
    margin-top: 7.9375mm;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 5mm;
    box-shadow: 0 0 5mm rgba(0,0,0,0.5);
    animation: sm-growth 0.2s ease-in; 
    transform-origin: top;
    size: auto;
}

.pageWrapper {
    min-height: 297mm;
    width: 210mm;
    display: flex;
    flex-direction: column;
}

@keyframes sm-growth {
    from {
        transform: scale(0.8);
    }
    to {
        transform: scale(1);
    }
}