/* Left side */

.leftSide {
    width: 46%;
    background-color: var(--primary-dark-color);
    color: #fff;
    padding: 6.6mm 3mm 6.6mm 6.6mm;
    min-height: 283.8mm;
}

.cvHeader {
    margin-bottom: 4mm;
    width: 58.981445833mm;
}

.cvHeading {
    padding: 0.79375mm 1.33mm 0.53mm;
    margin: 0 -1.33mm 0.53mm;
    font-size: 20pt;
    z-index: 2;
}

.cvJobDesc {
    padding: 1mm 1.33mm;
    margin: 0 -1.33mm;
    font-size: 11pt;
    z-index: 2;
}

.unitTitleWrapper {
    position: relative;
}

.unitTitleWrapper::after {
    width: 116%;
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: -11.1%;
    bottom: 0;
    right: 0;
    background-color: #39737b;
}

.unitTitle {
    padding: 0 1.33mm;
    margin: 0 -1.33mm 4mm;
    font-size: 13pt;
    line-height: 25pt;
    position: relative;
    z-index: 1;
}

.unitBody {
    font-size: 9pt;
}

.unitLine {
    margin-bottom: 2mm;
    z-index: 2;
    position: relative;
}

.lineTitleWrapper {
    position: relative;
}

.lineTitle {
    display: block;
    padding: 1mm;
    margin: 0 -1mm;
    font-weight: 600;
    z-index: inherit;
    position: relative;
}

.lineDescription {
    padding: 1mm;
    margin: 0 -1mm;
    position: relative;
    flex: 1;
}

/* Right side */

.rightSide {
    width: 100%;
    padding: 6.6mm;
    min-height: 100%;
    display: flex;
    flex-direction: column;
}

.unitBody {
    
}

.lineTextWrapper {
    width: 128.22369792mm;
}

.lineText {
    line-height: 15pt;
    font-size: 10pt;
    padding: 1mm;
    margin: -1mm -1mm 5mm;
    text-align: justify;
}

.iconWrapper {
    border-radius: 50%;
    background-color: var(--primary-dark-color);
    color: #fff;
    padding: 1mm 1.85mm;
    margin-right: 2mm;
}

.lineSubtitle {
    font-weight: bold;
    margin-left: 2.4971375mm;
    margin-top: -1mm;
    padding: 1mm;
}

.lineList {
    padding: 0 4.5mm;
    line-height: 15pt;
    margin-left: 2.4971375mm;
}

.cvFooter {
    flex: 1;
}

.copyright {
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
}

.copyrightText {
    color: rgb(143, 142, 142);
    font-size: 10pt;
}
